"use client";
import React, { useState, useEffect } from 'react';
import { Gift, formatCurrency } from '@/types';
import { Plus, DollarSign, Trash2, Link as LinkIcon, Image as ImageIcon, History, ChevronDown } from 'lucide-react';

interface AdminPanelProps {
  gifts: Gift[];
  onAddGift: (gift: Omit<Gift, 'id' | 'contributions'>) => void;
  onAddFunding: (giftId: string, amount: number, name: string) => void;
  onDeleteContributions: (giftId: string, contributionIds: string[]) => void;
  onReset: () => void;
  activeGift: Gift | undefined;
}

export const AdminPanel: React.FC<AdminPanelProps> = ({ gifts, onAddGift, onAddFunding, onDeleteContributions, onReset, activeGift }) => {
  const [isOpen, setIsOpen] = useState(false);
  
  // Selection State
  const [selectedGiftId, setSelectedGiftId] = useState<string>('');

  // New Gift State
  const [brand, setBrand] = useState('');
  const [name, setName] = useState('');
  const [price, setPrice] = useState('');
  const [imageUrl, setImageUrl] = useState('');
  const [productUrl, setProductUrl] = useState('');

  // Funding State
  const [fundingAmount, setFundingAmount] = useState('');
  const [donorName, setDonorName] = useState('');

  // History Selection State
  const [selectedIds, setSelectedIds] = useState<string[]>([]);

  // Initialize selectedGiftId when gifts change or on open
  useEffect(() => {
    if (activeGift) {
      setSelectedGiftId(activeGift.id);
    } else if (gifts.length > 0 && !selectedGiftId) {
      setSelectedGiftId(gifts[0].id);
    }
  }, [activeGift, gifts.length, isOpen]);

  // If the currently selected gift is deleted or invalid, fallback
  const currentGift = gifts.find(g => g.id === selectedGiftId) || gifts[0];

  const handleAddGift = (e: React.FormEvent) => {
    e.preventDefault();
    if (!brand || !name || !price || !imageUrl) return;

    onAddGift({
      brand,
      name,
      price: parseInt(price.replace(/[^0-9]/g, '')),
      imageUrl,
      productUrl
    });

    // Reset form
    setBrand('');
    setName('');
    setPrice('');
    setImageUrl('');
    setProductUrl('');
  };

  const handleAddFunding = (e: React.FormEvent) => {
    e.preventDefault();
    if (!fundingAmount || !currentGift) return;
    
    // Allow negative numbers by including '-' in the regex
    const amount = parseInt(fundingAmount.replace(/[^0-9-]/g, ''));
    if (isNaN(amount)) return;

    onAddFunding(currentGift.id, amount, donorName);
    setFundingAmount('');
    setDonorName('');
  };

  const toggleSelection = (id: string) => {
    setSelectedIds(prev => 
      prev.includes(id) ? prev.filter(i => i !== id) : [...prev, id]
    );
  };

  const handleBulkDelete = () => {
    if (!currentGift) return;
    if (confirm(`${selectedIds.length}개의 항목을 삭제하시겠습니까?`)) {
        onDeleteContributions(currentGift.id, selectedIds);
        setSelectedIds([]);
    }
  };

  if (!isOpen) {
    return (
      <button 
        onClick={() => setIsOpen(true)}
        className="fixed bottom-4 right-4 bg-gray-900 text-white px-4 py-2 rounded-full shadow-lg text-xs hover:bg-gray-800 transition-colors opacity-50 hover:opacity-100 z-50"
      >
        관리자 모드
      </button>
    );
  }

  return (
    <div className="fixed bottom-0 left-0 w-full bg-white border-t border-gray-200 shadow-[0_-10px_40px_rgba(0,0,0,0.1)] p-6 z-50 max-h-[85vh] overflow-y-auto">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-bold">관리자 패널</h2>
        <div className="flex gap-2">
            <button 
                onClick={onReset}
                className="text-red-500 text-sm hover:underline px-2"
            >
                전체 초기화
            </button>
            <button 
                onClick={() => setIsOpen(false)}
                className="text-gray-500 hover:text-black"
            >
                닫기
            </button>
        </div>
      </div>

      {/* Gift Selector */}
      {gifts.length > 0 && (
          <div className="mb-6">
              <label className="block text-xs font-medium text-gray-500 mb-1">편집할 선물 선택</label>
              <div className="relative">
                  <select
                      value={selectedGiftId}
                      onChange={(e) => {
                          setSelectedGiftId(e.target.value);
                          setSelectedIds([]); // Clear selection when switching
                      }}
                      className="w-full appearance-none p-3 border border-gray-300 rounded-lg bg-white text-sm font-medium focus:outline-none focus:ring-2 focus:ring-black"
                  >
                      {gifts.map(g => {
                          const isCompleted = g.contributions.reduce((s,c) => s + c.amount, 0) >= g.price;
                          return (
                              <option key={g.id} value={g.id}>
                                  {isCompleted ? '[완료] ' : '[진행중] '} {g.brand} - {g.name}
                              </option>
                          );
                      })}
                  </select>
                  <ChevronDown size={16} className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 pointer-events-none"/>
              </div>
          </div>
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-8">
        {/* Add Funding Section */}
        <div className="bg-gray-50 p-4 rounded-xl border border-gray-100">
          <h3 className="font-semibold mb-4 flex items-center gap-2">
            <DollarSign size={18} />
            입금 내역 추가
          </h3>
          {currentGift ? (
            <form onSubmit={handleAddFunding} className="space-y-4">
              <div>
                <label className="block text-xs font-medium text-gray-500 mb-1">선택된 선물</label>
                <div className="text-sm font-medium">{currentGift.brand} - {currentGift.name}</div>
              </div>
              <div className="grid grid-cols-2 gap-3">
                  <div>
                    <label className="block text-xs font-medium text-gray-500 mb-1">보낸 사람 (선택)</label>
                    <input
                      type="text"
                      value={donorName}
                      onChange={(e) => setDonorName(e.target.value)}
                      placeholder="익명"
                      className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-black focus:border-transparent outline-none"
                    />
                  </div>
                  <div>
                    <label className="block text-xs font-medium text-gray-500 mb-1">입금액 (원)</label>
                    <input
                      type="text"
                      value={fundingAmount}
                      onChange={(e) => setFundingAmount(e.target.value)}
                      placeholder="30,000 (음수 가능)"
                      className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-black focus:border-transparent outline-none"
                    />
                  </div>
              </div>
              <button 
                type="submit"
                className="w-full bg-black text-white py-2 rounded-md hover:bg-gray-800 transition-colors"
              >
                입금 확인
              </button>
            </form>
          ) : (
            <p className="text-gray-500 text-sm py-4">등록된 선물이 없습니다.</p>
          )}
        </div>

        {/* Add Gift Section */}
        <div className="bg-gray-50 p-4 rounded-xl border border-gray-100">
          <h3 className="font-semibold mb-4 flex items-center gap-2">
            <Plus size={18} />
            새 선물 등록
          </h3>
          <form onSubmit={handleAddGift} className="space-y-3">
            <div className="grid grid-cols-2 gap-3">
                <div>
                    <label className="block text-xs font-medium text-gray-500 mb-1">브랜드</label>
                    <input
                    type="text"
                    value={brand}
                    onChange={(e) => setBrand(e.target.value)}
                    placeholder="Byredo"
                    className="w-full p-2 border border-gray-300 rounded-md text-sm"
                    />
                </div>
                <div>
                    <label className="block text-xs font-medium text-gray-500 mb-1">가격 (원)</label>
                    <input
                    type="text"
                    value={price}
                    onChange={(e) => setPrice(e.target.value)}
                    placeholder="150,000"
                    className="w-full p-2 border border-gray-300 rounded-md text-sm"
                    />
                </div>
            </div>
            <div>
              <label className="block text-xs font-medium text-gray-500 mb-1">제품명</label>
              <input
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="블랑쉬 오드퍼퓸 50ml"
                className="w-full p-2 border border-gray-300 rounded-md text-sm"
              />
            </div>
            <div>
              <label className="block text-xs font-medium text-gray-500 mb-1 flex items-center gap-1">
                <ImageIcon size={12}/> 이미지 주소 (URL)
              </label>
              <input
                type="text"
                value={imageUrl}
                onChange={(e) => setImageUrl(e.target.value)}
                placeholder="https://example.com/image.jpg"
                className="w-full p-2 border border-gray-300 rounded-md text-sm"
              />
            </div>
            <div>
              <label className="block text-xs font-medium text-gray-500 mb-1 flex items-center gap-1">
                <LinkIcon size={12}/> 판매 사이트 링크 (URL)
              </label>
              <input
                type="text"
                value={productUrl}
                onChange={(e) => setProductUrl(e.target.value)}
                placeholder="http://..."
                className="w-full p-2 border border-gray-300 rounded-md text-sm"
              />
            </div>
            <button 
              type="submit"
              className="w-full bg-white border border-black text-black py-2 rounded-md hover:bg-gray-50 transition-colors"
            >
              선물 목록에 추가
            </button>
          </form>
        </div>
      </div>

      {/* Contribution History Log */}
      {currentGift && (
        <div className="border-t border-gray-200 pt-6">
            <div className="flex justify-between items-center mb-4">
                <h3 className="font-semibold flex items-center gap-2">
                    <History size={18} />
                    펀딩 내역 로그 ({currentGift.brand})
                </h3>
                {selectedIds.length > 0 && (
                    <button 
                        type="button"
                        onClick={handleBulkDelete}
                        className="text-xs bg-red-100 text-red-600 px-3 py-1 rounded-full font-medium hover:bg-red-200 transition-colors flex items-center gap-1"
                    >
                        <Trash2 size={12} />
                        선택 삭제 ({selectedIds.length})
                    </button>
                )}
            </div>
            
            {currentGift.contributions.length > 0 ? (
                <div className="bg-gray-50 rounded-lg border border-gray-100 overflow-hidden">
                    <div className="max-h-48 overflow-y-auto">
                        <table className="w-full text-sm text-left">
                            <thead className="bg-gray-100 text-gray-500 font-medium sticky top-0">
                                <tr>
                                    <th className="p-3 w-10 text-center">
                                        <input 
                                            type="checkbox" 
                                            onChange={(e) => {
                                                if (e.target.checked) {
                                                    setSelectedIds(currentGift.contributions.map(c => c.id));
                                                } else {
                                                    setSelectedIds([]);
                                                }
                                            }}
                                            checked={currentGift.contributions.length > 0 && selectedIds.length === currentGift.contributions.length}
                                            className="w-4 h-4 accent-black rounded cursor-pointer"
                                        />
                                    </th>
                                    <th className="p-3">날짜</th>
                                    <th className="p-3">보낸 사람</th>
                                    <th className="p-3 text-right">금액</th>
                                </tr>
                            </thead>
                            <tbody className="divide-y divide-gray-200">
                                {[...currentGift.contributions].reverse().map((contribution) => (
                                    <tr key={contribution.id} className="hover:bg-gray-100 transition-colors">
                                        <td className="p-3 text-center">
                                            <input 
                                                type="checkbox"
                                                checked={selectedIds.includes(contribution.id)}
                                                onChange={() => toggleSelection(contribution.id)}
                                                className="w-4 h-4 accent-black rounded cursor-pointer"
                                            />
                                        </td>
                                        <td className="p-3 text-gray-500 text-xs">
                                            {new Date(contribution.date).toLocaleString('ko-KR', {
                                                month: 'numeric',
                                                day: 'numeric',
                                                hour: '2-digit',
                                                minute: '2-digit'
                                            })}
                                        </td>
                                        <td className="p-3 font-medium">
                                            {contribution.name || '익명'}
                                        </td>
                                        <td className={`p-3 text-right font-mono ${contribution.amount < 0 ? 'text-red-500' : ''}`}>
                                            {formatCurrency(contribution.amount)}
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            ) : (
                <div className="text-center py-8 text-gray-400 text-sm bg-gray-50 rounded-lg border border-dashed border-gray-200">
                    아직 입금 내역이 없습니다.
                </div>
            )}
        </div>
      )}
    </div>
  );
};

export default AdminPanel;